import React from 'react';

const wrapperStyles = {
    marginLeft: 'auto',
    marginRight: '24px',
    width: 'max-content',
    border: '1px solid',
    borderRadius: '10px',
    padding: '16px'
}

const paragraphStyles = {
    margin: '0',
}

export const InfoWidget = ({testCount, passCount}) => {
    return (
      <div style={wrapperStyles}>
        <p style={paragraphStyles}>Всего тестов - {testCount}</p>
        <p style={paragraphStyles}>Пройдено - {passCount}</p>
      </div>
    );
}
